import React, { Component } from "react";
import AppProvider from "./AppProvider";
import Main from "./components/Main";
import Footer from "./components/Footer";
import "./App.css";

class App extends Component {
    render() {
        return (
            <AppProvider>
                <div className="tictactoe">
                    <Main />
                    <Footer />
                </div>
            </AppProvider>
        );
    }
}

export default App;
